	import {
		getUserAuth,
		auth2Page
	} from '../../../utils/login.js'
	import {
		showPic,
		navTo
	} from '../../../utils/default.js'
	export default {
		data() {
			return {
				userAuth: false,
				userInfo: null
			}
		},
		methods: {
			showPic,
			navTo,
			getUserAuth(e) {
				if (getUserAuth(e)) {
					this.userAuth = true;
					this.userInfo = getApp().globalData.userInfo;
				}
			},
},
		onLoad() {
			console.log('Mine onLoad');
			this.userInfo = getApp().globalData.userInfo;
			getApp().loginCB = () => {
				console.log('loginCB');
				auth2Page.call(this)
				this.userInfo = getApp().globalData.userInfo;
			}
		},
		onShow() {

		}
	}
